* {
  margin: 0;
  padding: 0;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}

html,
body {
  width: 100%;
  height: 100%;
}

body {
  font-size: 0.32rem;
  font-family: 'Microsoft YaHei';
}

:global(#root) {
  width: 100%;
  height: 100%;
}

:global(.vip) {
  position: relative;
  height: 100%;
  text-align: center;

  min-width: 320px;
  max-width: 750px;
  margin: 0 auto;
}

input {
  outline: none;
  border: none;
}

::-webkit-input-placeholder {
  font-size: 0.32rem;
  color: #ccc;
}

:-ms-input-placeholder {
  font-size: 0.32rem;
  color: #ccc;
}

::-ms-input-placeholder {
  font-size: 0.32rem;
  color: #ccc;
}

::placeholder {
  font-size: 0.32rem;
  color: #ccc;
}

button {
  display: block;
  font-size: 0.32rem;
  border: none;
  background-color: transparent;
  outline: none;
}

a {
  color: #000;
  text-decoration: none;
}

ul {
  list-style: none;
}


